}
.formWindow__main {
  width: 20vw;
  min-height: 20vw;
  margin: 0 auto;
}
.formWindow__logoWrapper {
  width: 50vw;
  height: 50vw;
  position: absolute;
}
.formWindow__logoWrapper--left {
  top: 60%;
  left: 0;
  -webkit-transform: translate(-50%, -50%) scale(0.33);
  transform: translate(-50%, -50%) scale(0.33);
}
.formWindow__logoWrapper--bottom {
  bottom: 2%;
  left: 90%;
  -webkit-transform: translate(-50%, 50%) scale(0.33);
  transform: translate(-50%, 50%) scale(0.33);
}

.logo {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: #ffd95080;
}
.logo::before {
  content: "";
  width: 50%;
  height: 50%;
  position: absolute;
  top: -5%;
  left: -15%;
  background-color: #5fb44180;
  box-shadow: -2vw 2vw 6vw 0 #5fb44180;
}
.logo::after {
  content: "";
  width: 50%;
  height: 50%;
  position: absolute;
  bottom: -5%;
  right: -15%;
  background-color: #f1559680;
  box-shadow: -2vw 2vw 6vw 0 #f1559680;
}
.logo--rotate45 {
  -webkit-transform: translate(-50%, -50%) rotateZ(45deg);
  transform: translate(-50%, -50%) rotateZ(45deg);
}
.logo--rotate315 {
  -webkit-transform: translate(-50%, -50%) rotateZ(315deg);
  transform: translate(-50%, -50%) rotateZ(315deg);
}

.formHeader {
  position: relative;
  width: 80%;
  height: 100%;
  margin: 0 auto;
}
.formHeader__progressLine {
  width: 100%;
  height: 4px;
  box-sizing: border-box;
  border: 2px solid #e9e0db;
}
.formHeader__doneLine {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 4px;
  box-sizing: border-box;
  border: 2px solid #bc8041;
}

.progressLine {
  position: relative;
}
.progressLine__point {
  position: absolute;
  top: 0;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 1;
  width: 0.7692307692vw;
  height: 0.7692307692vw;
  box-sizing: border-box;
  border: 2px solid #e9e0db;
  border-radius: 50%;
  background-color: #bc8041;
}
.progressLine__point--completed {
  width: 1.6vw;
  height: 1.6vw;
  background-color: #fff;
  box-shadow: inset 0 0 0 0.4vw #bc8041;
}
.progressLine__point--contacts {
  left: 0;
}
.progressLine__point--address {
  left: 33.3%;
}
.progressLine__point--categories {
  left: 66.6%;
}
.progressLine__point--success {
  left: 100%;
}
.progressLine__marker {
  position: absolute;
  left: 0;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 2.4096385542vw;
  height: 2.4096385542vw;
