  border: 2px solid #e9e0db;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: inset 0 0 0 0.701754386vw #bc8041;
}
.progressLine__subscription {
  position: absolute;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  top: 2vw;
  font-size: 1vw;
  font-weight: bold;
